/* eslint-disable react/jsx-props-no-spreading */
import React from 'react';
import * as Dialog from '@radix-ui/react-dialog';

type DialogProps = Dialog.DialogProps & {
  children?: React.ReactNode;
  className?: string,
  overlayClassName?: string,
}

export const CustomizedDialog = ({
  children, className, overlayClassName, ...restProps
}: DialogProps) => (
  <Dialog.Root {...restProps}>
    <Dialog.Portal>
      <Dialog.Overlay className={`${overlayClassName} fixed inset-0 z-40 h-full min-h-[100vh] bg-background-dark transition-all md:opacity-80`} />
      <Dialog.Content className={`${className} fixed top-0 left-0 z-50 w-screen items-center overflow-auto md:top-1/2 md:left-1/2 md:flex md:w-fit md:-translate-x-1/2 md:-translate-y-1/2`}>
        {children}
      </Dialog.Content>
    </Dialog.Portal>
  </Dialog.Root>
);

export { CustomizedDialog as Dialog };
