/* eslint-disable no-underscore-dangle */
import { Processor } from 'domain/equipment/processor';
import { Resolution } from 'domain/equipment/resolution';
import { IGatsbyImageData } from 'gatsby-plugin-image';

export type Performance = readonly ({
  readonly name: string | null;
  readonly fps: number | null;
  readonly imgFile: {
      readonly childImageSharp: {
          readonly gatsbyImageData: IGatsbyImageData;
      } | null;
  } | null;
} | null)[] | null

export type PerformanceQuery = {
  readonly [Resolution.FHD]: Performance,
  readonly [Resolution.QHD]: Performance,
  readonly [Resolution.UHD]: Performance,
}

export type GalleryData = {
  image: IGatsbyImageData,
  fps: number,
  name: string,
  processor: Processor,
  resolution: Resolution,
  priceCategory: string,
}[]

export const convertToGalleryData = (
  nodes: PerformanceQuery,
  processor: Processor,
  priceCategory: string,
) => Object.entries(nodes).reduce((result, [key, value]) => ([
  ...result,
  ...value!.map((item) => ({
    image: item?.imgFile?.childImageSharp?.gatsbyImageData!,
    fps: item?.fps!,
    name: item?.name!,
    processor: processor!,
    resolution: key as Resolution,
    priceCategory,
  })),
]), [] as GalleryData);
