import { Resolution } from 'domain/equipment/resolution';
import React from 'react';
import { ToggleGroup } from '../toggle-group/toggle-group';

interface ResolutionToggleGroupProps {
 value: Resolution;
 onChange: (value: Resolution) => void;
 className?: string;
}

export const ResolutionToggleGroup = ({
  value,
  onChange,
  className,
}: ResolutionToggleGroupProps) => (
  <ToggleGroup
    className="w-full justify-evenly gap-8"
    value={value}
    onChanged={onChange}
    items={[
      {
        id: Resolution.FHD,
        element: <span className="text-sm font-extrabold ">1080</span>,
        className,
      },
      {
        id: Resolution.QHD,
        element: <span className="text-sm font-extrabold">1440</span>,
        className,
      },
      {
        id: Resolution.UHD,
        element: <span className="text-sm font-extrabold">4K</span>,
        className,
      },
    ]}
  />
);
