/* eslint-disable react/jsx-props-no-spreading */
import React from 'react';
import * as ToggleGroup from '@radix-ui/react-toggle-group';

const Item = ({ className, ...restProps }: ToggleGroup.ToggleGroupItemProps) => (
  <ToggleGroup.Item
    className={`${className} data-[state=on]:border-gradient group text-secondary-gray data-[state=on]:border-b-2 data-[state=on]:text-main-white`}
    {...restProps}
  />
);

interface ToggleGroupItem<T extends string> {
  id: T,
  element: React.ReactElement,
  ariaLabel?: string,
  className?: string,
}

interface ToggleGroupProps<T extends string> {
  defaultValue?: T,
  value?: T,
  onChanged?: (value: T) => void,
  items: ToggleGroupItem<T>[],
  className?: string,
  ariaLabel?: string,
}

export const CustomizedToggleGroup = <T, >({
  items,
  value,
  onChanged: setValue,
  defaultValue,
  className,
  ariaLabel,
}: ToggleGroupProps<T extends string ? T : never>) => (
  <ToggleGroup.Root
    className={`${className} flex`}
    type="single"
    defaultValue={defaultValue}
    aria-label={ariaLabel}
    value={value}
    onValueChange={(value) => {
      if (setValue && value) setValue(value as T extends string ? T : never);
    }}
  >
    {items.map((item) => (
      <Item
        key={item.id}
        value={item.id}
        aria-label={item.ariaLabel}
        className={item.className}
      >
        {item.element}
      </Item>
    ))}
  </ToggleGroup.Root>
  );

export { CustomizedToggleGroup as ToggleGroup };
