/* eslint-disable react/jsx-props-no-spreading */
import { GatsbyImage, IGatsbyImageData } from 'gatsby-plugin-image';
import React from 'react';
import { FpsCounter } from '../fps-counter/fps-counter';

const FpsCounterContainer = (props: React.HTMLAttributes<HTMLDivElement>) => (
  <div {...props} className="absolute bottom-0 right-0 translate-x-1 translate-y-2 md:left-1/2 md:-translate-x-1/2 md:translate-y-1/2" />
);

interface GameCoverProps {
  image: IGatsbyImageData,
  fps: number,
  alt?: string,
  className?: string,
}

export const GameCover = (props: GameCoverProps) => {
  const {
    image, alt, fps, className,
  } = props;
  return (
    <div className={`${className} relative flex h-full items-center justify-center`}>
      <GatsbyImage image={image} alt={alt || ''} className="h-full w-full rounded-lg" />
      <FpsCounterContainer>
        <FpsCounter value={fps} />
      </FpsCounterContainer>
    </div>
  );
};
