import { BorderedCircle } from 'components/atoms/bordered-circle/bordered-circle';
import React, { useRef } from 'react';
import CountUp from 'react-countup';

interface FpsMeasureProps {
  value: number,
}

export const FpsCounter = ({
  value,
}: FpsMeasureProps) => {
  const prevValue = useRef(0);
  const updatePrevValue = () => {
    prevValue.current = value;
  };
  return (
    <BorderedCircle>
      <CountUp
        duration={0.25}
        delay={0}
        start={prevValue.current}
        end={value}
        onReset={updatePrevValue}
        onStart={updatePrevValue}
        onUpdate={updatePrevValue}
        onEnd={updatePrevValue}
      >
        {({ countUpRef }) => (
          <span className="font-uni-sans font-extrabold" ref={countUpRef}>
            {value}
          </span>
        )}
      </CountUp>

      <span className="text-xs">FPS</span>
    </BorderedCircle>
  );
};
