/* eslint-disable no-underscore-dangle */
/* eslint-disable react/jsx-props-no-spreading */
/* eslint-disable react/prop-types */
import { GameCover } from 'components/molecues/game-cover/game-cover';
import { IGatsbyImageData } from 'gatsby-plugin-image';
import React from 'react';

interface GamesFpsGalleryProps {
  fpsData: {
    image: IGatsbyImageData;
    fps: number;
    name: string;
}[],
  className?: string,
}

const GamesFpsGallery = (props: GamesFpsGalleryProps) => {
  const { fpsData, className, ...restProps } = props;

  return (
    <div {...restProps} className={`grid h-full w-fit grid-cols-2 items-center gap-4 pb-8 md:gap-4 md:gap-y-10 [&>*]:col-span-1 ${className}`}>
      {fpsData?.map((item) => (
        <GameCover key={item.name} image={item.image} fps={item.fps} className="col-span-1 aspect-[3/4] columns-auto md:h-fit md:max-h-48" />
      ))}
    </div>
  );
};

export { GamesFpsGallery };
